
.dialogs{

    display: flex;
    flex-direction: row;
    border: 3px solid silver;
    margin-bottom: 20px;
    padding: 5px 10px;
    font-size: 15px;



}

.photo {



    width: 50px;
    height: 50px;

    overflow: hidden;
    border-radius: 50%;


}
.kartinka{
    width: auto;
    height: 100%;

}
.nameDialog{
    color: black;
    font-size: 18px;
    margin: auto 0 auto 3px;
    transition: 0.2s linear color;

}
.nameDialog:hover{
    color: lavender;
    border-bottom: 2px solid black;
}

.active{
    border-bottom: 2px solid black;
    font-weight: bold;
}